.swiper_customers{
  width: 100%;
  height: 437px;
  position: relative;
}

.slider-customer {
    position: relative;
    width: 770px !important;
    height: 357px !important;
    margin: 0;
}

@media (max-width:991px) {
    .swiper_customers{
        height: 330px;
    }
    .slider-customer{
        width: 450px !important;
        height: 250px !important;
    } 
}

@media (max-width:500px) {
    .swiper_customers{
        height: 310px;
    }
    .slider-customer{
        width: 343px !important;
        height: 250px !important;
    } 
}
  
.swiper-slide img {
    width: 100%;
    height: 100%;
    border-radius: 2rem;
    object-fit: cover;
}

.swiper_customers .swiper-pagination .swiper-pagination-bullet{
    width: 16px;
    height: 16px;
    background: #fff;
    opacity: 1;
}
  
.swiper_customers .swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active{
    position: relative;
}
  
.swiper_customers .swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active::before{
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 20px;
    height: 20px;
    border: 1px solid #fff;
    border-radius: 50%;
}
